.cart-overlay {
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.2);
  top: 0;
  left: 0;
  position: fixed;
  z-index: 999;
}
.cart-sider {
  position: fixed;
  top: 3rem;
  right: -100%;
  background: #fff;
  border-radius: 10px;
  width: 500px;
  height: 80vh;
  box-shadow: 2px 0 10px rgba(0, 0, 0, 0.2);
  transition: right 0.3s ease-in-out;
  z-index: 1000;
  overflow-y: auto;
}
.cart-sider.open {
  right: 0;
}
.cartSider-header {
  display: flex;
  justify-content: space-between;
  /* background: red; */
  align-items: center;
  padding: 15px;
  border-bottom: 1px solid #000;
}
.cartSider-header h3 {
  font-size: 20px;
  font-family: sans-serif;
  font-weight: bold;
  line-height: 1.4;
}
.cartSider-header h3 span {
  font-size: 18px;
  font-family: "Times New Roman", Times, serif;
  font-weight: lighter;
}
.cartSider-header .close-button{
    font-size: 25px;
}
.cartSider-header .close-button:hover{
    color: #767676;
}
.cart-content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* background: red; */
    max-width: 350px;
    margin: 5rem auto;
}